@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}.App{display:flex;height:100vh}.main-calculator{display:flex;margin:auto;width:335px;height:520px;border:1px solid #707070;background-color:#393939;border-radius:54px}.lightgrey-calculator{display:flex;flex-direction:column;margin:auto;margin-top:60px;width:308px;height:431px;background-color:#e1e0df;box-shadow:5px 5px 10px #000000;border-radius:2px}#display-shadow{display:flex;margin:auto;margin-top:30px;width:290px;height:79px;background-color:#706e6c;border-radius:7px}#display{margin:auto;width:284px;height:72px;background-color:#c7c7ae;border:0.2 solid #707070;box-shadow:2px 2px 3px #2e2e2e;border-radius:1px}#display h1,#display h2{text-align:right;margin-right:10px}#display h2{font-size:36px}#display h1{font-size:24px}.button{margin:auto;display:grid;grid-template-columns:repeat(4, 65px);grid-template-rows:repeat(5, 48px);justify-content:center;grid-column-gap:13px;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;grid-row-gap:9px;row-gap:9px;width:294px;height:282px}.buttons{outline:none;display:flex;justify-content:center;align-items:center;width:65px;height:48px;border-radius:7px;background-color:#FDFAF7;border:none;box-shadow:inset 2px -1px 1px 0 #ffffff33,  inset -3px 2px 5px 0 #00000033,  1px -1px 1px 0 #4e4e4e9e, -1px 1px 5px 0 #0000004a;font-family:'Rubik', sans-serif;color:#4A4A4A;font-weight:500;text-shadow:0 0 2px #4a4a4a57;text-align:center;font-size:26px;cursor:pointer}.buttons:active{transform:translate(1px, -1px);box-shadow:inset 0px 0px 1px 0 #ffffff33,  inset 0px 0px 5px 0 #00000033,  0px 0px 2px 0 #4e4e4e9e,  0px 0px 2px 0 #0000004a}#clear,#divide,#multiply,#subtract,#add{background-color:#868686;color:#FDF9F8;text-shadow:0 0 2px #ffffffe8;box-shadow:inset -1px 1px 5px 0 #00000033,  1px -1px 1px 0 #393939,  -1px 1px 5px 0px #0000006b}#clear:active,#divide:active,#multiply:active,#subtract:active,#add:active{transform:translate(1px, -1px);box-shadow:inset 0px 0px 1px 0 #ffffff33,  inset 0px 0px 5px 0 #00000033,  0px 0px 2px 0 #4e4e4e9e,  0px 0px 2px 0 #0000004a}#clear{width:139px;grid-column:1 / span 2}#equals{background-color:#FF8067;color:#FDF9F8;height:106px;grid-row:4 / span 2;grid-column:4}#zero{width:139px;grid-column:1 / span 2}

